<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col md="12">
        <b-card>
          <div slot="header">
            Cari <strong>JUKIR</strong> dengan QR
          </div>
          <b-button class="buttonLarge" variant="success"  @click="searchJukirByQr">
            <i class="fa fa-qrcode" style="font-size: 6.1em; vertical-align: center"></i><br>
          </b-button>
        </b-card>
      </b-col>

      <b-col md="12">
        <b-card>
          <div slot="header">
            Cari <strong>JUKIR</strong>
          </div>
          <b-form>
            <b-form-group validated
                          label="ID Jukir"
                          label-for="idJukir"
                          description="Silahkan masukkan id jukir">
              <b-form-input v-model="idJukir" id="idJukir" type="text" placeholder="" required/>
            </b-form-group>
            <div slot="footer">
              <b-button size="sm" variant="primary" @click="searchJukirByString()"><i class="fa fa-search"></i> Cari</b-button>&nbsp;&nbsp;
              <b-button type="reset" size="sm" variant="danger"><i class="fa fa-ban"></i> Reset</b-button>
            </div>
          </b-form>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
  export default {
    name: 'cari-jukir',

    data() {
      return {
        idJukir :"",
        errorMsg: ''
      }
    },

    methods: {
      searchJukirByQr()
      {
        this.$router.push('show-qr-jukir');
      },
      searchJukirByString()
      {
        this.$router.push({
          path:'info/info-jukir',
          query: {id: this.idJukir}
        });
      },
    },
  };
</script>
